.header {
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: transparent;
  position: absolute;
  top: 30px;
  padding: 0 9%;
  &-logo {
    width: 100px;
    cursor: pointer;
    z-index: 4;
  }
  &-logo img {
    width: 100%;
  }
}
