.petItem {
  width: 22%;
  height: 255px;
  border-radius: 10px;
  box-shadow: 8px 8px 16px #848484, -8px -8px 16px #ffffff;
  position: relative;
  cursor: pointer;
  @media (max-width: 1200px) {
    width: 30%;
  }
  @media (max-width: 900px) {
    width: 45%;
  }
  @media (max-width: 768px) {
    width: 80%;
    height: 415px;
  }
  @media (max-width: 600px) {
    width: 90%;
    height: 390px;
  }
  @media (max-width: 500px) {
    width: 100%;
    height: 286px;
  }
  @media (max-width: 500px) {
    height: 260px;
  }
  &-imgWrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 10px;
  }
  &-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &-tooltip {
    display: block;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    position: absolute;
    background-color: var(--color3);
    border-radius: 5px;
    padding: 10px;
    bottom: 0;
    left: 50%;
    transform: translate(-50%);
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    color: var(--color4);
  }
  &:hover &-tooltip {
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
  }
}
